}

.CartHeader-CartIcon {
   padding: 8px;
}

.CartHeader-ViewCartBtn {
    padding: 6px 20px;color: #0B090A;background: #F5F3F4;font-family: 'Roboto Condensed', sans-serif;box-shadow: 04px 04px 2px;
}
